import React, {useState} from 'react';
import "./SignIn.css";
import {Button} from "@material-ui/core"
import axios from "axios"
import {useHistory} from "react-router-dom"


const SignIn = ({setLoginUser}) => {

    const history = useHistory();

    const[user, setUser] = useState({
        username : '',
        password : ''
    })

    function handleChange(event){
        const {name, value} = event.target;

        setUser(user => {
            return{
                ...user,
                [name] : value
            }
        })

    }

    function handleClick(event) {

        // const user = {
        //     username : user.username,
        //     password : user.password  
        // }

        axios.post("https://fullstacksignupsign47.herokuapp.com/app/SignIn", user)
        .then(response => {
         alert(response.data.message)
         setLoginUser(response.data.signedupUser)
         history.push("/");
        })

    }

    return (

        <div className = "SignIn">
                <h1>THERAPY ~ BOX</h1>
                <input type = "text" placeholder = "Type your Username" onChange = {handleChange} name = "username" value = {user.username}></input>
                <input type = "password" placeholder = "Type a unique password" onChange = {handleChange} name = "password" value = {user.password}></input>
                <Button style={{width: '80%', backgroundColor: 'yellow', borderRadius: 10}} onClick = {handleClick} >Login</Button>
                <div>
                    <hr></hr>
                   <p>OR</p> 
                </div>
                <div className = "button" onClick = {() => history.push("/SignUp")}>
                        New to the challenge? Sign up 
                </div>
        </div>

    )
    }

export default SignIn
